@import url("https://fonts.googleapis.com/css2?family=Ibarra+Real+Nova&family=Montserrat:wght@100;200;300&family=Sofia+Sans+Condensed:wght@700&family=Source+Serif+4:opsz,wght@8..60,500&family=Spectral:ital,wght@0,200;1,200&display=swap");
@font-face {
  font-family: "Metric";
  src: url("../../fonts/Metric-Regular.woff2") format("woff2");
  font-weight: normal;
  font-style: normal;
}
@font-face {
  font-family: "Metric";
  src: url("../../fonts/Metric-Thin.woff2") format("woff2");
  font-weight: 100;
  font-style: normal;
}
@font-face {
  font-family: "Metric";
  src: url("../../fonts/Metric-Medium.woff2") format("woff2");
  font-weight: 500;
  font-style: normal;
}
@font-face {
  font-family: "Metric";
  src: url("../../fonts/Metric-Bold.woff2") format("woff2");
  font-weight: 900;
  font-style: normal;
}
@font-face {
  font-family: "Metric";
  src: url("../../fonts/Metric-SemiBold.woff2") format("woff2");
  font-weight: 600;
  font-style: normal;
}
html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font: inherit;
  vertical-align: baseline;
  text-decoration: none;
}

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
  display: block;
}

body {
  line-height: 1;
}

ol, ul {
  list-style: none;
}

blockquote, q {
  quotes: none;
}

blockquote:before, blockquote:after,
q:before, q:after {
  content: "";
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

* {
  box-sizing: border-box;
}

html {
  scroll-behavior: smooth;
}

img {
  max-width: 100%;
  width: 100%;
}

body {
  font-family: system-ui, -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen, Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
}

header {
  width: 100%;
  top: 0;
  z-index: 1000;
  background-color: #f7f0e8;
  overflow: hidden;
}
header .header-wrapper {
  position: relative;
  display: grid;
  grid-template-columns: 1fr 2fr 1fr;
  font-size: 1.625rem;
  letter-spacing: -0.3px;
  text-transform: uppercase;
  font-family: "Montserrat", sans-serif;
  padding: 5px 14px 5px 5px;
  align-items: center;
  justify-content: center;
}
header .header-wrapper nav ul {
  display: flex;
  flex-direction: row;
  font-weight: 100;
}
header .header-wrapper .row-3 {
  display: flex;
  justify-content: end;
  align-items: center;
}
header .header-wrapper .row-3 h4 {
  padding-right: 14px;
}

.posts {
  position: relative;
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-auto-rows: 75vh;
}
.posts .post {
  font-size: 12px;
  padding: 20px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  position: relative;
}
.posts .post .post-content {
  display: none;
}
.posts .post footer {
  display: flex;
  justify-content: space-between;
  letter-spacing: 1px;
}
.posts .post:first-child {
  background-color: yellow;
}
.posts .post:nth-child(2) {
  background-color: green;
}
.posts .post:nth-child(3) {
  background-color: skyblue;
}
.posts .post:nth-child(4) {
  background-color: orange;
}